.teacher {
  @media (max-width: $mobile) {
    padding-left: 5px;
    padding-right: 5px;
  }
}

.teacher__title {
  margin-top: 0;
  margin-bottom: 10px; //30
  font-size: 18px;
  font-weight: bold;
  line-height: 1.2;

  @media (max-width: $mobile) {
    margin-bottom: 9px;

    font-size: 15px;
    line-height: 15px;
  }
}

.teacher__list {
  display: flex;
  justify-content: space-between;

  .swiper-slide {
    width: auto;
    padding: 20px 8px;
  }

  @include list-reset;

  @media (max-width: $mobile) {
    flex-direction: column;
  }
}

.teacher__item {
  // width: 18% !important;
  width: 100%;
  padding: 10px 10px 20px 10px;
  border-radius: 10px;
  box-shadow: 0 0 8px rgba($color: #000000, $alpha: 0.1);

  @media (max-width: $mobile) {
    display: flex;
    align-items: center;

    width: 100%;
    margin-bottom: 10px;
    padding: 10px;
  }
}

.teacher__img-link {
  display: block;
  text-decoration: none;

  @media (max-width: $mobile) {
    margin-right: 10px;
  }
}

.teacher__img {
  border-radius: 10px;
  display: block;

  @media (max-width: $mobile) {
    width: 44px;
    height: 44px;
  }
}

.teacher__link {
  font-size: 11px;
  font-weight: bold;
  line-height: 1.2;
  color: $black;
  text-decoration: none;
  transition: color .3s;

  &:hover,
  &:focus {
    color: $main-color;
  }

  @media (max-width: $mobile) {
    font-size: 14px;
    line-height: 19px;
  }
}
